import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import functions from '../views/function.vue'
import login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home,
    children: [ // 二级路由的配置
      {
        path: 'KnowledgeA',
        component: () => import('../views/home/KnowledgeA'),
        children: [
          {
            path: 'calculator',
            component: () => import('../components/calculator')
          },
          {
            path: 'calendar',
            component: () => import('../components/calendar')
          },
          {
            path: 'memo',
            component: () => import('../components/memo')
          },
          {
            path: 'drawKG',
            component: () => import('../components/drawFunction/drawKG')
          }
        ]
      },
      {
        path: 'KnowledgeM',
        component: () => import('../views/home/KnowledgeM')
      },
      {
        path: 'KnowledgeI',
        component: () => import('../views/home/KnowledgeI')
      },
      {
        path: 'SystemS',
        component: () => import('../views/home/SystemS')
      },
      {
        path: 'ProductT',
        component: () => import('../views/home/ProductT')
      },
      {
        path: 'ProductF',
        component: () => import('../views/home/ProductF')
      }
    ]
  },
  {
    path: '/information',
    name: 'information',
    component: functions,
    children: [ // 二级路由的配置
      {
        path: 'info',
        component: () => import('../views/home/information')
      },
      {
        path: 'KnowledgeI',
        component: () => import('../views/home/KnowledgeI')
      }
    ]
  },
  { // 默认路由的设置
    path: '/login',
    name: 'login',
    component: login
  },
  { // 默认路由的设置
    path: '/',
    name: 'login',
    component: login
  }
]

const router = new VueRouter({
  routes
})

export default router
